'use client';

import { useRouter } from 'next/navigation';
import { useState } from 'react';
import Link from 'next/link';

export default function RegisterPage() {
    const router = useRouter();
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [name, setName] = useState('');
    const [error, setError] = useState('');

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');

        try {
            const res = await fetch('/api/register', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    username,
                    password,
                    name,
                }),
            });

            if (res.ok) {
                router.push('/auth/login');
            } else {
                const data = await res.json();
                setError(data.message || 'ÌöåÏõêÍ∞ÄÏûÖ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
            }
        } catch (err) {
            console.log(err);
            setError('ÌöåÏõêÍ∞ÄÏûÖ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
        }
    };

    return (
        <div className="flex min-h-screen items-center justify-center p-4">
            <div className="w-full max-w-md card">
                <div className="text-center mb-6">
                    <h1 className="text-3xl font-bold text-[var(--primary)] mb-2">Join Rocket üöÄ</h1>
                    <p className="text-[var(--text-secondary)]">ÏÉàÎ°úÏö¥ Í∞ÄÏ°± Íµ¨ÏÑ±Ïõê Îì±Î°ù</p>
                </div>

                <form onSubmit={handleSubmit} className="space-y-4">
                    <div>
                        <label className="block text-sm font-medium mb-1">Ïù¥Î¶Ñ (ÎãâÎÑ§ÏûÑ)</label>
                        <input
                            type="text"
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            className="w-full p-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-[var(--primary)] text-black"
                            placeholder="Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
                            required
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-medium mb-1">ÏïÑÏù¥Îîî</label>
                        <input
                            type="text"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            className="w-full p-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-[var(--primary)] text-black"
                            placeholder="ÏïÑÏù¥ÎîîÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
                            required
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-medium mb-1">ÎπÑÎ∞ÄÎ≤àÌò∏</label>
                        <input
                            type="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            className="w-full p-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-[var(--primary)] text-black"
                            placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
                            required
                        />
                    </div>

                    {error && <p className="text-[var(--error)] text-sm">{error}</p>}

                    <button
                        type="submit"
                        className="w-full btn-primary py-3 text-lg shadow-lg hover:shadow-xl transform transition-all"
                    >
                        Í∞ÄÏûÖÌïòÍ∏∞
                    </button>
                </form>

                <div className="mt-4 text-center text-sm text-[var(--text-secondary)]">
                    Ïù¥ÎØ∏ Í≥ÑÏ†ïÏù¥ ÏûàÏúºÏã†Í∞ÄÏöî? <Link href="/auth/login" className="text-[var(--primary)] font-bold hover:underline">Î°úÍ∑∏Ïù∏</Link>
                </div>
            </div>
        </div>
    );
}
